// tag标签
import uniTag from "@/components/uni-ui/uni-tag/uni-tag.vue"
import uniFav from '@/components/uni-ui/uni-fav/uni-fav.vue' 

export default {
	props: ['games'],
	components: {
		uniTag,
		uniFav
	},
	data() {
		return {
		};
	},
	// 接收父组件传过来的数据
	// props: ['games'],
	methods: {
		isUnknowTime(date){
			const flag = this.$common.isUnknowTime(date)
			if(flag) return "待定"
			return date
		},
		navigator(id) {
			console.log(id)
			this.$emit('click', id)
		},
		onClickCollect(){
			this.collectChecked = !this.collectChecked
		},
		type(i) {
			if(i == 0) return 'success'
			if(i == 1) return 'warning' 
			return 'primary'
		},
		getGameType(i){
			return this.$api.getGameType(i)
		},
		isEnd(e){
			return this.$api.isEnd(e)
		}
	}
}
